<template>
  <div class="account">
    <div class="top">
      <van-nav-bar title="个人详情" left-text="返回" left-arrow @click-left="goleft" />
    </div>
    <div class="accountbox">
      <div class="body">
        <div class="bodyitem portrait">
          <div class="bodyitem-le">
            <span>头像</span>
          </div>
          <div class="bodyitem-ri">
            <div class="imgbox">
              <img :src="userInfo.userImg" alt />
            </div>
            <van-uploader class="uploaderbox" :after-read="afterRead" />
            <van-icon name="arrow" size="20" />
          </div>
        </div>
        <div class="bodyitem username">
          <div class="bodyitem-le">
            <span>用户名</span>
          </div>
          <div class="bodyitem-ri">
            <input
              type="text"
              v-model="userInfo.nickName"
              @change="changeUserInfo('nickName', '/updateNickName')"
            />
            <van-icon name="arrow" size="20" />
          </div>
        </div>
        <div class="bodyitem tel">
          <div class="bodyitem-le">
            <span>手机号</span>
          </div>
          <div class="bodyitem-ri">
            <strong>{{userInfo.phone}}</strong>
            <van-icon name="arrow" size="20" />
          </div>
        </div>
        <div class="bodyitem address">
          <div class="bodyitem-le">
            <span>id号</span>
          </div>
          <div class="bodyitem-ri">
            <strong>{{userInfo.userId}}</strong>
            <van-icon name="arrow" size="20" />
          </div>
        </div>
      </div>
      <div class="foot logout" @click="logoutuser">退出登录</div>
    </div>
  </div>
</template>

<script>
import "../assets/less/account.less";
import { utils } from "../assets/js/utils";
export default {
  created() {
    this.getUserInfo();
  },
  data() {
    return {
      maxSize: 204800,
      userInfo: {},
    };
  },
  methods: {
    //返回上一级
    goleft() {
      this.$router.go(-1);
    },
    //上传头像
    afterRead(file) {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$$router.push("/login");
      }
      //
      // 获取文件类型
      let type = file.file.type.split("/")[1];

      let imgType = ["png", "jpg", "jpeg", "gif"];
      if (imgType.indexOf(type) === -1) {
        this.$notify({
          message: "图片类型只支持" + imgType.join(","),
          color: "#fff",
          background: "#3a63f1",
        });
        return;
      }
      //获取文件的大小
      if (this.maxSize < file.file.size) {
        this.$notify({
          message: "图片大小最大只能为" + this.maxSize / 1024 + "KB",
          color: "#fff",
          background: "#3a63f1",
        });
        return;
      }
      // 获取base64
      let base64 = file.content.replace(/data:image\/[A-Za-z]+;base64,/, "");

      //序列化参数
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        imgType: true,
        serverBase64Img: base64,
      });
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url: "/updateAvatar",
        data,
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == "H001") {
            this.userInfo.userImg = result.data.userImg;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //获取个人资料
    getUserInfo() {
      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findAccountInfo",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == "B001") {
            this.userInfo = result.data.result[0];
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //修改资料
    changeUserInfo(key, url) {
      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push("/login");
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        [key]: this.userInfo[key],
      });

      this.axios({
        method: "POST",
        url,
        data,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == "B001") {
            this.userInfo = result.data.result[0];
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //退出登录
    logoutuser() {
      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push("/login");
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
      });

      this.axios({
        method: "POST",
        url: "/logout",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == "F001") {
            localStorage.removeItem("CSTK");
            this.$router.push({ name: "Login" });
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
  },
};
</script>

<style lang="less" scoped>
</style>